import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import Header from "./header"
import "./layout.css"
import 'typeface-montserrat';

const navigations = [
  {name: 'Work', route: '/work'}, 
  {name: 'Agency', route: '/agency'}, 
  {name: 'Contact', route: '/contact'}
];
const navigationsRu = [
  {name: 'Наши проекты', route: '/ru/work'}, 
  {name: 'О нас', route: '/ru/agency'}, 
  {name: 'Контакты', route: '/ru/contact'}
];

const Layout = ({ children, ru }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div className="page-view">
        <div className="page-view-inner">
          {children}
          <Header siteTitle={data.site.siteMetadata.title} navigations={ru ? navigationsRu : navigations} ru={ru ? ru : 'null'} />
        </div>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
