import React from 'react';
import Fade from 'react-reveal/Fade';
import './main-header.css';

const MainHeader = ({ title }) => (
    <Fade bottom>
        <div className="column">
            <h1 className="header is-in-viewport">
                {title}
            </h1>
        </div>
    </Fade>
)

export default MainHeader;