import React from "react"
import PropTypes from "prop-types"
import { Link } from 'gatsby';
import Headroom from 'react-headroom';
import Fade from 'react-reveal/Fade';
import Logo from '../images/abstract_logo.svg';
import LogoDark from '../images/abstract_logo-dark.svg';
import usaFlag from '../images/usa-flag.svg';
import ruFlag from '../images/ru-flag.svg';
import './header.css';

export default class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isOpen: false }
  };
  toggleNav = (e) => {
    e.preventDefault();
    this.setState({ isOpen: !this.state.isOpen })
  };
  render() {
    const { siteTitle, navigations, ru } = this.props;
    return (
      <>
        <Headroom disableInlineStyles>
          <Fade top>
            <div className="background">
              <div className="container column">
                <div className="nav-item">
                  <Link to="/" className="is-active logo-block">
                    <img src={Logo} alt={siteTitle} className="logo logo-light" />
                    <img src={LogoDark} alt={`${siteTitle} dark`} className="logo logo-dark" />
                  </Link>
                </div>
                <div className="nav-stack">{navigations.map((obj, i) => (
                  <div className="nav-item" key={i}>
                    <Link to={`/${obj.route}`}
                      className={`at-left button-secondary is-active`}>
                      <span>{obj.name}</span>
                    </Link>
                  </div>
                ))}{console.log(ru)}
                <div className="nav-item dropdown">
                  <span className="at-left">
                    <img src={ru === 'null' ? usaFlag : ruFlag} alt="select language" />
                  </span>
                  <div className="dropdown-content">
                    <Link to="/work">
                      <img src={usaFlag} alt="USA flag"/>
                      <span className="lang-dark">English</span>
                    </Link>
                    <Link to="/ru/work">
                      <img src={ruFlag} alt="RU flag"/>
                      <span className="lang-dark">Русский</span>
                    </Link>
                  </div>
                </div>
                </div>
                <div className="nav-stack open-navigation">
                  <div className="nav-item">
                    <a href="/" className="at-left button-secondary" onClick={this.toggleNav}>
                      <span>Menu</span>
                      <span className="button-secondary--icon icon icon-burger"></span>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </Fade>
        </Headroom>
        <div className={`navigation ${this.state.isOpen ? 'is-visible' : null}`}>
          <div className="navigation-inner">
            <a href="/" className="close button-secondary" onClick={this.toggleNav}>
              Close
                <span className="button-secondary--icon icon icon-close"></span>
            </a>
            <Fade bottom>
              <ul>
                { navigations.map((obj, i) => (
                  <li key={i}><Link to={obj.route}>{obj.name}</Link></li>
                )) }
              </ul>
              <p className="navigation-footer">
                <a href="/http://fb.com/abstractvs">Facebook</a>
                <a href="/http://youtube.com/abstractvs">YouTube</a>
                <a href="/http://instagram.com/abstractvs">Instagram</a>
                <span>
                  <span className="symbol-copy">&copy;</span>
                  2019, Abstract Visuals Inc.
                </span>
              </p>
            </Fade>
          </div>
        </div>
      </>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}
